import React from "react";
import { useTranslation } from "react-i18next";

import "./VaLoanContract.css";

function VaLoanContract() {
  const { t } = useTranslation();
  return (
    <div
      id="va-loan-contract"
      className="fha-loan-contract light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-9.png"
              alt="/images/loan-guide/s-9.png"
            />
            <p className="header-text-3">{t("vaLoan.section_6.head")}</p>
            <h4 className="header-text-1">{t("vaLoan.section_6.sub")}</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_6.txt_1")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">{t("vaLoan.section_6.txt_2")}</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_6.txt_3")}
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_6.txt_4")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">{t("vaLoan.section_6.txt_5")}</h3>
            <hr className="loan-details-hr" />
            <ul>
              <li>
                <p className="sub-text-1">{t("vaLoan.section_6.txt_6")}</p>
              </li>
              <li>
                <p className="sub-text-1">{t("vaLoan.section_6.txt_7")}</p>
              </li>
              <li>
                <p className="sub-text-1">{t("vaLoan.section_6.txt_8")}</p>
              </li>
              <li>
                <p className="sub-text-1">{t("vaLoan.section_6.txt_9")}</p>
              </li>
              <li>
                <p className="sub-text-1">{t("vaLoan.section_6.txt_10")}</p>
              </li>
            </ul>
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_6.txt_11")}
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_6.txt_12")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>{t("vaLoan.back_to_top")}</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default VaLoanContract;
