import React from "react";
import { useTranslation } from "react-i18next";

import NavBar from "../../../components/NavBar/NavBar";
import StepCard from "../../../components/StepCard/StepCard";
import VaLoanEligibility from "./VaLoanEligibility/VaLoanEligibility";
import WhatVaLoan from "./WhatVaLoan/WhatVaLoan";
import VaLoanBenefits from "./VaLoanBenefits/VaLoanBenefits";
import VaLoanRates from "./VaLoanRates/VaLoanRates";
import VaLoanRefinance from "./VaLoanRefinance/VaLoanRefinance";
import VaLoanContract from "./VaLoanContract/VaLoanContract";
import ApplyVaLoan from "./ApplyVaLoan/ApplyVaLoan";

import "./VALoanGuide.css";

function VALoanGuide() {
  const { t } = useTranslation();
  return (
    <div>
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <h4 className="header-text-1">{t("vaLoan.head")}</h4>
            <h3 className="header-text-2">{t("vaLoan.sub_1")}</h3>
            <p className="header-text-3">{t("vaLoan.sub_2")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#what-va-loan",
                topTxt: t("vaLoan.section_1.head"),
                subTxt: t("vaLoan.section_1.sub"),
                imgSrc: "/images/loan-guide/s-1.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#va-loan-eligibility",
                topTxt: t("vaLoan.section_2.head"),
                subTxt: t("vaLoan.section_2.sub"),
                imgSrc: "/images/loan-guide/s-3.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#va-loan-benefits",
                topTxt: t("vaLoan.section_3.head"),
                subTxt: t("vaLoan.section_3.sub"),
                imgSrc: "/images/loan-guide/s-4.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#va-loan-rates",
                topTxt: t("vaLoan.section_4.head"),
                subTxt: t("vaLoan.section_4.sub"),
                imgSrc: "/images/loan-guide/s-5.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#va-loan-refinancing",
                topTxt: t("vaLoan.section_5.head"),
                subTxt: t("vaLoan.section_5.sub"),
                imgSrc: "/images/loan-guide/s-9.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#va-loan-contract",
                topTxt: t("vaLoan.section_6.head"),
                subTxt: t("vaLoan.section_6.sub"),
                imgSrc: "/images/loan-guide/s-7.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-9 center mb-5">
            <a href="#apply-va-loan" className="step-card">
              <div className="row">
                <div className="col-9 col-sm-12">
                  <h5>{t("vaLoan.section_7.head")}</h5>
                  <p>{t("vaLoan.section_7.sub")}</p>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
      <WhatVaLoan />
      <VaLoanEligibility />
      <VaLoanBenefits />
      <VaLoanRates />
      <VaLoanRefinance />
      <VaLoanContract />
      <ApplyVaLoan />
    </div>
  );
}

export default VALoanGuide;
