import React from "react";
import { useTranslation } from "react-i18next";

import "./WhatVaLoan.css";

function WhatVaLoan() {
  const { t } = useTranslation();
  return (
    <div
      id="what-va-loan"
      className="what-va-loan dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-1.png"
              alt="/images/loan-guide/s-1.png"
            />
            <p className="header-text-3">{t("vaLoan.section_1.head")}</p>
            <h4 className="header-text-1">{t("vaLoan.section_1.sub")}</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("vaLoan.section_1.txt_1")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">{t("vaLoan.section_1.txt_2")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">{t("vaLoan.section_1.txt_3")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">{t("vaLoan.section_1.txt_4")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">{t("vaLoan.section_1.txt_5")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>{t("fhaLoan.back_to_top")}</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WhatVaLoan;
